import React, { useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import MainHeader from "../../components/Users/MainHeader";
import "bootstrap/dist/css/bootstrap.min.css";

import {
  SectionWrapper,
  Wrapper,
} from "../../components/Styles/RegisterStyles";
import { FaEye } from "react-icons/fa"; // FontAwesome icons
import "react-big-calendar/lib/css/react-big-calendar.css";
import ModuleSideNav from "../../components/Users/ModuleSideNav";
import axios from "axios";
import { DataRoomSection } from "../../components/Styles/DataRoomStyle.js";
import { Button } from "../../components/Styles/MainStyle.js";
import CompanyShareReferralCode from "../../components/Users/popup/CompanyShareReferralCode.jsx";
import DataTable from "react-data-table-component";
import { Link } from "react-router-dom";
import { FaShareAlt } from "react-icons/fa";
export default function Referralcode() {
  var apiUrl = "https://blueprintcatalyst.com/api/user/";
  document.title = "Share Referral Code";
  useEffect(() => {
    getallsharedCodeByCompany();
  }, []);
  //
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [records, setrecords] = useState([]);
  const customStyles = {
    rows: {
      style: {
        minHeight: "60px",
        overflow: " visible",
      },
    },
    headCells: {
      style: {
        fontSize: "14px",
        fontWeight: "bold",
        overflow: "visible",
        "& div": {
          overflow: "visible",
        },
      },
    },
  };
  const [searchText, setSearchText] = useState("");
  const [SharedDiscount, setSharedDiscount] = useState(false);
  const getallsharedCodeByCompany = async () => {
    let formData = {
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(
        apiUrl + "getallsharedCodeByCompany",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data;
      setrecords(respo.results);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const returnrefresh = () => {};
  const filteredData = records.filter((item) => {
    const name = `${item.company_name || ""} - ${item.update_date || ""} - ${
      item.version || ""
    }`;
    return (
      name.toLowerCase().includes(searchText.toLowerCase()) ||
      (item.update_date || "")
        .toLowerCase()
        .includes(searchText.toLowerCase()) ||
      (item.download || "").toLowerCase().includes(searchText.toLowerCase())
    );
  });
  const columns = [
    {
      // name: 'Date of Report',
      name: "Shared Email",
      selector: (row) => row.email,
      sortable: true,
    },
    {
      // name: 'Date of Report',
      name: "Code",
      selector: (row) => row.discount_code,
      sortable: true,
    },
    {
      // name: 'Date of Report',
      name: "Discount",
      selector: (row) => row.percentage + "%",
      sortable: true,
    },

    {
      name: "Action",
      cell: (row) => (
        <div className="d-flex gap-2">
          <Link
            to={`/share/referralcodetracking/${row.id}/${row.discount_code}`}
            rel="noopener noreferrer"
            className="btn btn-sm btn-outline-primary"
            title="View Usage Code"
          >
            <FaEye />
          </Link>
        </div>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const handlesharepopup = () => {
    setSharedDiscount(true);
  };
  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <MainHeader />
          <SectionWrapper className="d-block py-5">
            <div className="container-lg">
              <div className="row">
                <div className="col-md-3">
                  <ModuleSideNav />
                </div>
                <div className="col-md-9">
                  <DataRoomSection className="d-flex flex-column gap-2">
                    <div className="titleroom d-flex justify-content-between align-items-center border-bottom pb-3">
                      <h4>Share Referral Code List</h4>
                      <Button
                        onClick={handlesharepopup}
                        type="button"
                        className="btn btn-outline-dark active"
                      >
                        Share <FaShareAlt />{" "}
                      </Button>
                    </div>
                    <div className="d-flex justify-content-end p-0">
                      <input
                        type="search"
                        placeholder="Search Here..."
                        className="form-control"
                        value={searchText}
                        onChange={(e) => setSearchText(e.target.value)}
                        style={{
                          padding: "10px",
                          width: "100%",
                          maxWidth: "200px",
                          fontSize: "14px",
                        }}
                      />
                    </div>
                    <div className="d-flex flex-column justify-content-between align-items-start tb-box">
                      <DataTable
                        customStyles={customStyles}
                        columns={columns}
                        className="datatb-report"
                        data={filteredData}
                        pagination
                        highlightOnHover
                        striped
                        responsive
                      />
                    </div>
                  </DataRoomSection>
                </div>
              </div>
            </div>
          </SectionWrapper>
        </div>
      </Wrapper>
      {SharedDiscount && (
        <CompanyShareReferralCode
          onClose={() => setSharedDiscount(false)}
          returnrefresh={returnrefresh}
        />
      )}
    </>
  );
}
