import React, { useState, useEffect } from "react";
import {
  Overlay,
  ModalContainer,
  ModalTitle,
  CloseButton,
  DropArea,
  ModalBtn,
  ButtonGroup,
} from "../../Styles/DataRoomStyle.js";
import axios from "axios";
import { Button } from "bootstrap";
import DataTable from "react-data-table-component";
import SuccessAlert from "../../../components/Admin/SuccessAlert";
import DangerAlert from "../../../components/Admin/DangerAlert";
const CompanyShareReferralCode = ({ onClose, returnrefresh }) => {
  const apiUrl = "https://blueprintcatalyst.com/api/user/";
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [emails, setEmails] = useState([""]);
  const [spinners, setspinners] = useState(false);
  const [errr, seterrr] = useState(false);
  const [successresponse, setsuccessresponse] = useState("");
  const handleSubmit = async (e) => {
    e.preventDefault();
    const allEmails = Array.from(
      new Set([...emails.filter((e) => e.trim() !== "")])
    );

    let payload = {
      shared_by: "Company",
      discount_code: e.target.code.value,
      emails: allEmails,
      user_id: userLogin.id,
    };
    if (allEmails.length === 0) {
      setsuccessresponse("Please provide at least one email.");
      seterrr(true);
      setTimeout(() => {
        seterrr(false);
        setsuccessresponse("");
      }, 2000);
      return;
    }

    setspinners(true);
    try {
      const res = await axios.post(apiUrl + "checkReferralUser", payload, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      setspinners(false);
      setsuccessresponse(res.data.message);
      if (res.data.status === "2") {
        seterrr(true);
      }
      if (res.data.status === "1") {
        setEmails([""]);
        seterrr(false);
        setTimeout(() => {}, 2000);
        returnrefresh();
        e.target.code.value = "";
      }

      setTimeout(() => {
        setsuccessresponse("");
      }, 2000);
    } catch (err) {
      console.error("Submit error:", err);
    }
  };
  const handleEmailChange = (index, value) => {
    const updated = [...emails];
    updated[index] = value;
    setEmails(updated);
  };
  const handleRemoveEmail = (index) => {
    const updated = emails.filter((_, i) => i !== index);
    setEmails(updated);
  };
  const handleAddMoreEmail = () => {
    setEmails([...emails, ""]);
  };
  return (
    <Overlay>
      <ModalContainer style={{ maxWidth: "900px", maxHeight: "550px" }}>
        <form
          onSubmit={handleSubmit}
          method="post"
          action="javascript:void(0)"
          style={{ height: "100%", display: "flex", flexDirection: "column" }}
        >
          <CloseButton onClick={onClose}>Ã—</CloseButton>
          {successresponse && (
            <p className={errr ? " mt-3 error_pop" : "success_pop mt-3"}>
              {successresponse}
            </p>
          )}

          {/* ðŸ“§ Email Inputs */}
          <div className="formmodalShared">
            <ModalTitle className="aititle">Share Referral Code</ModalTitle>
            <label>
              Code <span className="text-danger">*</span>
            </label>
            <div className="form-group  d-flex gap-2 mb-4">
              <input
                type="text"
                required
                name="code"
                className="form-control"
                placeholder="Enter Code..."
              />
            </div>
            <label className="mb-2">
              Email <span className="text-danger">*</span>
            </label>
            {emails.map((email, index) => (
              <div key={index} className="form-group mb-2 d-flex gap-2">
                <input
                  type="email"
                  required
                  className="form-control"
                  placeholder="Enter email..."
                  value={email}
                  onChange={(e) => handleEmailChange(index, e.target.value)}
                />
                {index > 0 && (
                  <button
                    type="button"
                    className="btn btn-sm btn-danger"
                    onClick={() => handleRemoveEmail(index)}
                  >
                    Remove
                  </button>
                )}
              </div>
            ))}
          </div>

          {/* âž• Add / Submit Buttons */}
          <div className="form-group ">
            <div className="d-flex justify-content-between align-items-center flex-wrap">
              <button
                type="button"
                className="btn btn-outline-dark active addbtn"
                onClick={handleAddMoreEmail}
              >
                + Add More Email
              </button>

              <ButtonGroup className="d-flex gap-2">
                <ModalBtn
                  disabled={spinners}
                  variant="upload"
                  type="submit"
                  style={{ opacity: spinners ? 0.6 : 1 }}
                  className="submit d-flex align-items-center gap-2"
                >
                  Submit
                  {spinners && (
                    <div
                      className="white-spinner spinner-border spinneronetimepay m-0"
                      role="status"
                    >
                      <span className="visually-hidden"></span>
                    </div>
                  )}
                </ModalBtn>
              </ButtonGroup>
            </div>
          </div>
        </form>
      </ModalContainer>
    </Overlay>
  );
};

export default CompanyShareReferralCode;
